import {Shape as ShapeData} from '../../lib/shapes'
import { useMediaQuery } from 'react-responsive'

export class Color {
  constructor(readonly hex: string, readonly label: string){}
}

type Props = {
  shape: ShapeData,
  useMargin: boolean
}
export const Shape = ({shape, useMargin}: Props) => {
  const isSm = useMediaQuery({ query: '(min-width: 640px)' })
  const size = isSm ? '2.25rem' : '1.92rem';
  return (
    <div 
      aria-label={`${shape.color!.label} ${shape.label}`}
      tabIndex={0}
      role="listitem"
      className={`w-12 h-12 sm:w-14 sm:h-14 flex items-center justify-center ${useMargin? 'mx-0.5': ''} text-5xl text-center font-bold font-mono rounded ${shape.color!.hex === '#3F1F56'  ? 'dark:brightness-200':'dark:brightness-150'}`}>
      {shape.render({height: size, width: size, fill: shape.color!.hex})}
    </div>
  )
}
